<template>
  <div class="inviteUser">
    <div class="top">
      <img class="logo" :src="this.inviteUserInfo.UserLogo"/>
      <p>{{this.inviteUserInfo.username}}</p>
      <p>邀请您加入{{this.data.name}}</p>
      <div class="link">
        <img class="link-one1" src="../../static/images/link_one.png"/>
        <img class="link-one2" src="../../static/images/link_one.png"/>
      </div>
    </div>
    <div class="center">
      <img v-if="this.user.groupId > 0" class="logo" :src="this.data.logo"/>
      <img v-else class="logo" src="../../static/images/bookfan_logo.png"/>
      <p class="name">{{this.data.name}}</p>
      <p class="introduction" v-if="this.user.groupId > 0">
        <span>成员：</span>{{this.data.memberCount}}
        <span>藏书：</span>30
      </p>
      <p class="summary">{{this.data.intro}}</p>
    </div>
    <div class="foot">
      <mt-field v-if="phoneInputVisible" v-model="phone" type="tel" class="phone" placeholder="输入手机号"></mt-field>
      <mt-button @click.native="acceptInvite" type="primary" class="inviteButton">{{this.inviteButtonText}}</mt-button>
    </div>
  </div>
</template>

<script>
  import {Toast, Indicator} from 'mint-ui'
  import axios from 'axios'
  import wx from 'weixin-js-sdk'

  export default {
    name: 'invite-user',
    beforeRouteEnter (to, from, next) {
      let userId = to.query.userId
      let groupId = to.query.groupId
      // sign做下标记， 用于后台返回当前页面
      axios.post('/webApp/src/auth.php?sign=inviteUser&userId=' + userId + '&groupId=' + groupId).then(response => {
        const data = response.data
        if (data.code === 500) {
          window.location.href = data.data
        } else {
          // 如果授权通过则进行下一步
          next(vm => {
            next()
          })
        }
      })
    },
    data () {
      return {
        user: {},
        inviteUserInfo: {},
        data: {
          logo: '',
          name: '贝壳书友会',
          intro: '贝壳书友会是一款基于纸质图书的社交APP，登录后可以找到和您看过同样一本书的书友，共同分享阅读的快乐，您还可以参加书友会组织的各种好玩的线上线下活动，通过APP查看附近的书友，在TA的书架上发现有趣的书，双方自行联系线下借阅，以书会友。'
        },
        phone: '',
        phoneInputVisible: true,
        inviteButtonText: '接受邀请'
      }
    },
    methods: {
      // 获取邀请人信息
      getUser () {
        axios.get('/admin/servlet/user.php?t=getUser', {params: this.user}).then(response => {
          const data = response.data
          if (data.code === '1') {
            this.inviteUserInfo = data.user
          } else {
            Toast({message: data.message, position: 'bottom', duration: 2000})
          }
        })
      },
      // 获取书吧信息
      getGroupInfo () {
        axios.get('/admin/servlet/group.php?t=GetGroupInfoById', {
          params: {
            GroupId: this.user.groupId,
            UserId: this.user.userId
          }
        }).then(response => {
          const data = response.data
          if (data.code === '1') {
            this.data.name = data.GroupInfo.groupName
            this.data.logo = data.GroupInfo.groupLogo
            this.data.intro = data.GroupInfo.groupIntro
            this.data.memberCount = data.MemberCount
          } else {
            Toast({message: data.message, position: 'bottom', duration: 2000})
          }
        })
      },
      // 接受邀请
      acceptInvite () {
        // 邀请成功 或者已经邀请过就跳转到下载app页面
        if (!this.phoneInputVisible) {
          window.location.href = 'http://a.app.qq.com/o/simple.jsp?pkgname=com.android.reader'
          return false
        }
        if (!this.phone) {
          Toast({message: '手机号不能为空', position: 'bottom', duration: 2000})
          return false
        }
        if (!/^1[3|4|5|7|8][0-9]{9}$/.test(this.phone)) {
          Toast({message: '请输入正确的手机号', position: 'bottom', duration: 2000})
          return false
        }
        axios.get('/admin/servlet/user.php?t=userInvite', {
          params: {
            phone: this.phone,
            type: this.user.groupId > 0 ? 1 : 2,
            groupId: this.user.groupId,
            userId: this.user.userId
          }
        }).then(response => {
          const data = response.data
          if (data.code === '1' || data.code === '-3') {
            Toast({message: data.message, position: 'bottom', duration: 2000})
            this.phoneInputVisible = false
            this.inviteButtonText = '下载App'
          } else {
            Toast({message: data.message, position: 'bottom', duration: 2000})
          }
        })
      },
      wechatShare () {
        const self = this
        axios.get('/webApp/src/share/sample.php').then(response => {
          let wechatShareConfig = response.data
          wx.config({
            debug: false, // 调试模式
            appId: wechatShareConfig.appId,
            timestamp: wechatShareConfig.timestamp,
            nonceStr: wechatShareConfig.nonceStr,
            signature: wechatShareConfig.signature, // 后端生成的签名
            // 需要调用什么的分享接口 需要先指定
            jsApiList: [
              'checkJsApi',
              'onMenuShareTimeline',
              'onMenuShareAppMessage',
              'onMenuShareQQ',
              'onMenuShareWeibo',
              'onMenuShareQZone'
            ]
          })
          wx.ready(function () {
            // 在这里调用 API
            // 分享到朋友圈
            wx.onMenuShareTimeline({
              title: '我在贝壳书友会等你！', // 分享标题
              desc: '找到和您看过同样一本书的书友，共同分享阅读的快乐', // 描述
              link: 'http://m.bookfan.cn/webApp/page/invite/index.html#/invite/user?userId=' + self.user.userId + '&groupId=' + self.user.groupId, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: 'http://m.bookfan.cn/images/ic_launcher.png', // 分享图标
              success: function (res) {
              }
            })
            wx.onMenuShareAppMessage({
              title: '我在贝壳书友会等你！', // 分享标题
              desc: '找到和您看过同样一本书的书友，共同分享阅读的快乐', // 描述
              link: 'http://m.bookfan.cn/webApp/page/invite/index.html#/invite/user?userId=' + self.user.userId + '&groupId=' + self.user.groupId, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: 'http://m.bookfan.cn/images/ic_launcher.png', // 分享图标
              success: function (res) {
              }
            })
            wx.onMenuShareQQ({
              title: '我在贝壳书友会等你！', // 分享标题
              desc: '找到和您看过同样一本书的书友，共同分享阅读的快乐', // 描述
              link: 'http://m.bookfan.cn/webApp/page/invite/index.html#/invite/user?userId=' + self.user.userId + '&groupId=' + self.user.groupId, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: 'http://m.bookfan.cn/images/ic_launcher.png', // 分享图标
              success: function (res) {
              }
            })
            wx.onMenuShareWeibo({
              title: '我在贝壳书友会等你！', // 分享标题
              desc: '找到和您看过同样一本书的书友，共同分享阅读的快乐', // 描述
              link: 'http://m.bookfan.cn/webApp/page/invite/index.html#/invite/user?userId=' + self.user.userId + '&groupId=' + self.user.groupId, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: 'http://m.bookfan.cn/images/ic_launcher.png', // 分享图标
              success: function (res) {
              }
            })
            wx.onMenuShareQZone({
              title: '我在贝壳书友会等你！', // 分享标题
              desc: '找到和您看过同样一本书的书友，共同分享阅读的快乐', // 描述
              link: 'http://m.bookfan.cn/webApp/page/invite/index.html#/invite/user?userId=' + self.user.userId + '&groupId=' + self.user.groupId, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: 'http://m.bookfan.cn/images/ic_launcher.png', // 分享图标
              success: function (res) {
              }
            })
          })
        })
      }
    },
    mounted () {
      const self = this
      Indicator.open('加载中...')
      self.user = self.$route.query
      // 如果是吧主登录 则获取书吧信息
      if (self.user.groupId > 0) {
        self.getGroupInfo()
      }
      // 获取邀请人信息
      self.getUser()

      let interval = window.setInterval(function () {
        Indicator.close()
        window.clearInterval(interval)
      }, 500)

      // 获取分享配置
      self.wechatShare()
    }
  }
</script>

<style scoped>
  .inviteUser {
    height: 100%;
    margin: 0 10%;
    position: absolute;
    top: 0;
  }

  .top {
    height: auto;
    padding: 5px 10px;
    border-radius: 5px;
    margin-top: 80%;
    background: #ffffff;
    text-align: center;
  }

  .top p {
    margin: 10px 0;
  }

  .top .link {
    position: relative;
    margin-top: 25px;
  }

  .link .link-one1 {
    height: 62px;
    position: absolute;
    left: 15px;
    bottom: -46px;
  }

  .link .link-one2 {
    height: 62px;
    position: absolute;
    right: 15px;
    bottom: -46px;
  }

  .center {
    height: auto;
    padding: 5px 10px;
    border-radius: 5px;
    margin-top: 8%;
    background: #ffffff;
    text-align: center;
  }

  .center p {
    margin: 5px 0;
  }

  .center .summary {
    color: #909399;
  }

  .center .introduction span {
  }

  .center .logo {
    margin-top: 25px;
  }

  .center .name {
    margin: 10px 0;
  }

  .logo {
    border-radius: 10px;
    width: 80px;
    height: 80px;
  }

  .foot {
    margin-top: 20%;
  }

  .phone {
    height: 50px;
    border-radius: 5px;
    margin: 10px 0
  }

  .inviteButton {
    height: 50px;
    width: 100%;
    margin: 20px 0
  }

</style>



