import Vue from 'vue'
import Router from 'vue-router'
import Layout from '../components/Layout'
import Login from '../views/Login'
import InviteList from '../views/InviteList'
import InviteUser from '../views/InviteUser'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'layout',
      component: Layout,
      redirect: 'login',
      children: [
        {
          path: 'login',
          component: Login,
          meta: {
            title: '登录'
          }
        },
        {
          path: 'invite/list',
          component: InviteList,
          meta: {
            title: '邀请列表'
          }
        },
        {
          path: 'invite/user',
          component: InviteUser,
          meta: {
            title: '邀请好友'
          }
        }
      ]
    }
  ]
})



