<template>
  <div class="login">
    <div class="top"></div>
    <div class="center">
        <mt-field type="tel" class="phone" placeholder="输入手机号" v-model="user.LoginName"></mt-field>
        <mt-field type="number" class="captcha" placeholder="输入验证码" v-model="user.Smscode">
          <mt-button v-if="!sendMsgDisabled" class="captchaButton" @click.native="getCaptcha">获取验证码</mt-button>
          <mt-button v-if="sendMsgDisabled" :disabled="true" class="captchaButton" @click.native="getCaptcha">
            {{this.time + '秒后获取'}}
          </mt-button>
        </mt-field>
        <mt-button type="primary" @click.native="login" class="loginButton">登录</mt-button>
    </div>
    <div class="foot">
      <p><span>普通会员：</span>获取专属邀请页面，可邀请好友加入贝壳书友会。</p>
      <p><span>吧主：</span>获取专属邀请页面，可邀请好友加入贝壳书友会，并自动加入你的书吧</p>
    </div>
  </div>
</template>

<script>
  import {Toast, Indicator} from 'mint-ui'
  import axios from 'axios'
  export default {
    name: 'login',
    data () {
      return {
        user: {
          LoginName: '',
          Smscode: ''
        },
        wechatInfo: {},
        // 短信计时
        sendMsgDisabled: '',
        time: 60,
        // 验证码
        code: ''
      }
    },
    methods: {
      // 登录
      login () {
        const self = this
        if (!this.user.LoginName) {
          Toast({message: '手机号不能为空', position: 'bottom', duration: 2000})
          return false
        }
        if (!/^1[3|4|5|7|8][0-9]{9}$/.test(this.user.LoginName)) {
          Toast({message: '请输入正确的手机号', position: 'bottom', duration: 2000})
          return false
        }
        if (!this.user.Smscode) {
          Toast({message: '验证码不能为空', position: 'bottom', duration: 2000})
          return false
        }
        Indicator.open('登录中...')
        axios.post('/weixin/login', this.user).then(response => {
          let interval = window.setInterval(function () {
            Indicator.close()
            self.$router.push('/invite/list')
            window.clearInterval(interval)
          }, 1000)
        }).catch(function (error) {
          const data = error.response
          let interval = window.setInterval(function () {
            Indicator.close()
            Toast({message: data.data.data, position: 'bottom', duration: 2000})
            window.clearInterval(interval)
          }, 1000)
        })
      },
      // 获取验证码
      getCaptcha () {
        if (!this.user.LoginName) {
          Toast({message: '手机号不能为空', position: 'bottom', duration: 2000})
          return false
        }
        if (!/^1[3|4|5|7|8][0-9]{9}$/.test(this.user.LoginName)) {
          Toast({message: '请输入正确的手机号', position: 'bottom', duration: 2000})
          return false
        }

        axios.post('/weixin/captcha?mobile=' + this.user.LoginName).then(response => {
          this.countDown()
        }).catch(function (error) {
          const data = error.response
          Toast({message: data.data.data, position: 'bottom', duration: 2000})
        })
      },
      // 按钮计时
      countDown () {
        let self = this
        self.sendMsgDisabled = true
        let interval = window.setInterval(function () {
          if ((self.time--) <= 1) {
            self.time = 60
            self.sendMsgDisabled = false
            window.clearInterval(interval)
          }
        }, 1000)
      }
    },
    mounted () {
      const self = this
      axios.post('/weixin/store').then(response => {
        const data = response.data
        if (data.code === 201) {
          Indicator.open('登录中...')
          let interval = window.setInterval(function () {
            Indicator.close()
            self.$router.push('/invite/list')
            window.clearInterval(interval)
          }, 1000)
        }
      })
    }
  }
</script>

<style scoped>
  .login {
    position: absolute;
    top: 0;
    height: 100%;
    margin: 0 10%;
  }

  .center {
    margin-top: 100%;
  }

  .phone {
    height: 50px;
    border-radius: 5px;
  }

  .captcha {
    height: 50px;
    border-radius: 5px;
    margin: 10px 0
  }
  .captchaButton {
    font-size: 16px;
    margin-left: 10px;
    width: 110px;
  }

  .loginButton {
    height: 50px;
    width: 100%;
    margin: 10px 0
  }

  .foot {
    margin: 30px 0;
  }

  .foot p {
    margin: 10px 0;
  }

</style>



