<template>
  <div class="inviteData">
    <div class="top" v-if="!this.user.groupId > 0">
      <img class="logo" src="../../static/images/bookfan_logo.png"/>
      <p class="name">贝壳书友会</p>
      <p class="summary">贝壳书友会是一款基于纸质图书的社交APP，登录后可以找到和您看过同样一本书的书友，共同分享阅读的快乐，您还可以参加书友会组织的各种好玩的线上线下活动，通过APP查看附近的书友，在TA的书架上发现有趣的书，双方自行联系线下借阅，以书会友。</p>
      <div class="link">
        <!--使用相对路径 打包时会将图片转为 base64-->
        <img class="link-one1" src="../../static/images/link_one.png"/>
        <img class="link-one2" src="../../static/images/link_one.png"/>
      </div>
    </div>
    <div class="top" v-else>
      <img class="logo" :src="this.groupInfo.groupLogo"/>
      <p class="name">{{this.groupInfo.groupName}}</p>
      <p class="introduction">
        <span>成员：</span>{{this.groupInfo.memberCount}}
        <span>藏书：</span>10
      </p>
      <p class="summary">{{this.groupInfo.groupIntro}}</p>
      <div class="link">
        <img class="link-one1" src="../../static/images/link_one.png"/>
        <img class="link-one2" src="../../static/images/link_one.png"/>
      </div>
    </div>
    <div class="center">
      <p class="inviteTitle">{{inviteData.inviteCount > 0 ? inviteData.inviteCount + '人接收了邀请' : '尚未有好友接受您的邀请！'}}</p>
      <ul
        v-infinite-scroll="loadMore"
        infinite-scroll-distance="10">
        <li v-for="item in inviteData.list">
          <img :src="item.head"/>
          <div class="userInfo">
            <p>{{item.nickname}}</p>
            <p>{{item.phone}}</p>
          </div>
          <div class="date">{{item.created_at}}</div>
        </li>
        <div class="loadingBox" v-if="this.loading">
          <span>加载中...</span>
          <mt-spinner size="14" class="spinner" color="#26a2ff" type="triple-bounce"></mt-spinner>
        </div>
        <div class="loadingBox" v-if="!this.loading">
          <span v-if="inviteData.inviteCount > 0">已经到底啦！</span>
        </div>
      </ul>
    </div>
    <div class="foot">
      <p>点击右上角分享，邀请好友加入贝壳书友会。</p>
    </div>
  </div>
</template>

<script>
  import {Toast, Indicator} from 'mint-ui'
  import axios from 'axios'
  import wx from 'weixin-js-sdk'

  export default {
    data () {
      return {
        user: this.$route.query,
        inviteData: {
          page: 1,
          maxPage: 0,
          inviteCount: 0,
          list: []
        },
        groupInfo: {},
        loading: true
      }
    },
    methods: {
      loadMore () {
        setTimeout(() => {
          this.loading = true
          if (this.inviteData.page <= this.inviteData.maxPage) {
            this.getinviteData(this.inviteData.page)
          } else {
            this.loading = false
          }
        }, 1000)
      },
      getGroupInfo () {
        axios.get('/admin/servlet/group.php?t=GetGroupInfoById', {
          params: {
            GroupId: this.user.groupId,
            UserId: this.user.userId
          }
        }).then(response => {
          const data = response.data
          if (data.code === '1') {
            this.groupInfo = data.GroupInfo
            this.groupInfo.memberCount = data.MemberCount
          } else {
            Toast({message: data.message, position: 'bottom', duration: 2000})
          }
        })
      },
      getInviteData (page) {
        axios.get('/admin/servlet/user.php?t=getInvite&page=' + page + '&userId=' + this.user.userId + '&groupId=' + this.user.groupId).then(response => {
          const data = response.data
          if (Number(data.code) === 1) {
            // 数组不能push数组 故将返回的数组数据map循环 将一个一个数据push进数组
            data.list.map((item) => {
              this.inviteData.list.push(item)
            })
            this.inviteData.inviteCount = data.count
            this.inviteData.maxPage = Math.ceil(Number(data.count) / 5)
            this.inviteData.page++
          } else {
            Toast({message: data.message, position: 'bottom', duration: 2000})
          }
        })
      },
      // 微信分享
      wechatShare () {
        const self = this
        axios.get('/webApp/src/share/sample.php').then(response => {
          let wechatShareConfig = response.data
          wx.config({
            debug: false, // 调试模式
            appId: wechatShareConfig.appId,
            timestamp: wechatShareConfig.timestamp,
            nonceStr: wechatShareConfig.nonceStr,
            signature: wechatShareConfig.signature, // 后端生成的签名
            // 需要调用什么的分享接口 需要先指定
            jsApiList: [
              'checkJsApi',
              'onMenuShareTimeline',
              'onMenuShareAppMessage',
              'onMenuShareQQ',
              'onMenuShareWeibo',
              'onMenuShareQZone'
            ]
          })
          wx.ready(function () {
            // 在这里调用 API
            // 分享到朋友圈
            wx.onMenuShareTimeline({
              title: '我在贝壳书友会等你！', // 分享标题
              desc: '找到和您看过同样一本书的书友，共同分享阅读的快乐', // 描述
              link: 'http://m.bookfan.cn/webApp/page/invite/index.html#/invite/user?userId=' + self.user.userId + '&groupId=' + self.user.groupId, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: 'http://m.bookfan.cn/images/ic_launcher.png', // 分享图标
              success: function (res) {
              }
            })
            wx.onMenuShareAppMessage({
              title: '我在贝壳书友会等你！', // 分享标题
              desc: '找到和您看过同样一本书的书友，共同分享阅读的快乐', // 描述
              link: 'http://m.bookfan.cn/webApp/page/invite/index.html#/invite/user?userId=' + self.user.userId + '&groupId=' + self.user.groupId, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: 'http://m.bookfan.cn/images/ic_launcher.png', // 分享图标
              success: function (res) {
              }
            })
            wx.onMenuShareQQ({
              title: '我在贝壳书友会等你！', // 分享标题
              desc: '找到和您看过同样一本书的书友，共同分享阅读的快乐', // 描述
              link: 'http://m.bookfan.cn/webApp/page/invite/index.html#/invite/user?userId=' + self.user.userId + '&groupId=' + self.user.groupId, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: 'http://m.bookfan.cn/images/ic_launcher.png', // 分享图标
              success: function (res) {
              }
            })
            wx.onMenuShareWeibo({
              title: '我在贝壳书友会等你！', // 分享标题
              desc: '找到和您看过同样一本书的书友，共同分享阅读的快乐', // 描述
              link: 'http://m.bookfan.cn/webApp/page/invite/index.html#/invite/user?userId=' + self.user.userId + '&groupId=' + self.user.groupId, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: 'http://m.bookfan.cn/images/ic_launcher.png', // 分享图标
              success: function (res) {
              }
            })
            wx.onMenuShareQZone({
              title: '我在贝壳书友会等你！', // 分享标题
              desc: '找到和您看过同样一本书的书友，共同分享阅读的快乐', // 描述
              link: 'http://m.bookfan.cn/webApp/page/invite/index.html#/invite/user?userId=' + self.user.userId + '&groupId=' + self.user.groupId, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: 'http://m.bookfan.cn/images/ic_launcher.png', // 分享图标
              success: function (res) {
              }
            })
          })
        })
      }
    },
    mounted () {
      const self = this
      axios.post('/weixin/invite').then(response => {
        const data = response.data
        self.inviteData.list = data.data.inviteList
        self.inviteData.inviteCount = data.data.inviteList.length
        self.user.groupId = data.data.groupInfo.groupId
        self.groupInfo.memberCount = 31
        self.groupInfo.groupLogo = data.data.groupInfo.groupLogo
        self.groupInfo.groupIntro = data.data.groupInfo.groupIntro
      })
      Indicator.open('加载中...')

      // 无法直接关闭 需要用定时器
      let interval = window.setInterval(function () {
        Indicator.close()
        window.clearInterval(interval)
      }, 500)

      // 获取分享配置
      // self.wechatShare()
    }
  }
</script>

<style scoped>
  .inviteData {
    position: absolute;
    top: 0;
    height: 100%;
    margin: 0 10%;
  }
  .top {
    height: auto;
    padding: 5px 10px;
    border-radius: 5px;
    margin-top: 80%;
    background: #ffffff;
    text-align: center;
  }

  .top p {
    margin: 10px 0;
  }

  .top .summary {
    color: #909399;
  }

  .top .link {
    position: relative;
    margin-top: 25px;
  }

  .link .link-one1 {
    height: 62px;
    position: absolute;
    left: 15px;
    bottom: -46px;
  }

  .link .link-one2 {
    height: 62px;
    position: absolute;
    right: 15px;
    bottom: -46px;
  }

  .logo {
    width: 80px;
    height: 80px;
    border-radius: 10px;
  }

  .center {
    border-radius: 5px;
    background: #ffffff;
    overflow: scroll;
    height: 50%;
    padding: 5px 10px;
    margin-top: 8%;
  }

  .center .inviteTitle {
    margin-top: 25px;
    text-align: center;
  }

  .center ul {
    list-style-type: none;
  }

  .center li {
    display: flex;
    margin: 5px 0;
  }

  .center li img {
    width: 60px;
    height: 60px;
  }

  .center li div {
    flex: 1;
  }

  .center .userInfo {
    display: flex;
    flex-direction: column;
  }

  .center .userInfo p {
    line-height: 30px;
    margin-left: 5px;
    flex: 1;
    width: 80px;
    overflow:hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .center .loadingBox {
    margin-top: 10px;
    margin-left: 30%;
  }

  .center .date {
    line-height: 60px;
  }

  .center .spinner {
    float: left;
    margin-right: 10px;
  }

  .loadingBox span {
    font-size: 14px;
  }

  .foot {
    margin: 20px 0;
  }
</style>



